import styled from 'styled-components';

export const Container = styled.div`
  display: flex;
  align-items: center;
`;

export const Avatar = styled.p`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  height: 35px;
  margin-right: 4px;
  border: 2px solid rgb(133, 133, 133);
  border-radius: 50%;
  font-size: 25px;
  font-weight: 400;
`;

export const Name = styled.span`
  font-weight: 700;
  margin-right: 4px;
`;
